import { HttpClient } from '@angular/common/http';

export class CharacterService {
    private http: HttpClient;

    constructor(http: HttpClient) {
        this.http = http;
    }

    public getCharacter(team: string, name: string): Promise<string> {
        return this.http.get<string>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/characters/getcharacter/' + team + '/' + name).toPromise();
    }

    public getPrice(team: string, name: string): Promise<number> {
        return this.http.get<number>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/characters/getprice/' + team + '/' + name).toPromise();
    }

    public getHTML(team: string, name: string): Promise<string> {
        return this.http.get<string>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/characters/gethtml/' + team + '/' + name).toPromise();
    }

    public getTeam(team: string): Promise<string[]> {
        return this.http.get<string[]>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/characters/getteam/' + team).toPromise();
    }
}