import { HttpClient } from '@angular/common/http';
import { User } from './user';

export class UserServices {
    private http: HttpClient;

    constructor(http: HttpClient) {
        this.http = http;
    }

    public Login(username: string, password: string): Promise<User> {
        return this.http.get<User>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/user/login/' + username + '/' + password).toPromise();
    }
    public Win(username: string, id: number) {
        if (id < 1000)
            return this.http.get<User>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/user/win/' + username + '/' + id).toPromise();
        return this.http.get<User>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/user/win/' + username + '/1000').toPromise();
    }
}