import { HttpClient } from '@angular/common/http';

export class GameServices {
    private http: HttpClient;

    constructor(http: HttpClient) {
        this.http = http;
    }

    public getUnits(): Promise<string[]> {
        return this.http.get<string[]>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/game/getunits').toPromise();
    }
    public getPlayers(): Promise<string[]> {
        return this.http.get<string[]>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/game/getplayers').toPromise();
    }
    public init(team: string, id: number): Promise<boolean> {
        return this.http.get<boolean>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/game/init/' + team + '/' + id).toPromise();
    }
    public play(id: number): Promise<boolean> {
        return this.http.get<boolean>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/game/play/' + id).toPromise();
    }
    public end(id: number): Promise<boolean> {
        return this.http.get<boolean>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/game/end/' + id).toPromise();
    }
    public buy(id: number, player: number, unit: string): Promise<boolean> {
        return this.http.get<boolean>('http://castledefensegame-env-2.acznmbp2nz.us-east-1.elasticbeanstalk.com/api/game/buy/' + id + '/' + player + '/' + unit).toPromise();
    }
}